 } }		// 	z-index: 10
.view-live {
	color: #fff;
	text-transform: uppercase;
	text-decoration: none;
	color: #fff;
	text-transform: uppercase;
	text-decoration: none;
	height: 34px;
	position: absolute;
	bottom: 15%;
	left: 100%;
	line-height: 34px;
	width: 100px;
	border-bottom: 4px solid #4094FF;
	text-align: center;
	transition: all .4s linear; }
